html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/*
 * Font Guide: To get [Font Name] use the corresponding rules.
 *
 * Proxima Nova Black
 * font-family: proxima-nova, sans-serif;
 * font-weight: 900;
 * font-style: normal;
 *
 * Proxima Nova Extrabold
 * font-family: proxima-nova, sans-serif;
 * font-weight: 800;
 * font-style: normal;
 *
 * Proxima Nova Regular
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: normal;
 *
 * Proxima Nova Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: italic;
 *
 * Proxima Nova Light
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: normal;
 *
 * Proxima Nova Light Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: italic;
 *
 * Proxima Nova Medium Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 500;
 * font-style: italic;
 */
html {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
}
body {
  color: rgba(0, 0, 0, 0.87);
  font-size: 1.6rem;
}
