}
.secondary-text {
  color: rgba(0, 0, 0, 0.6);
}
.secondary-text.inverted {
  color: rgba(255, 255, 255, 0.7);
}
.help-text {
  color: rgba(0, 0, 0, 0.38);
}
.help-text.inverted {
  color: rgba(255, 255, 255, 0.5);
}
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
.ce-form .ce-f-row + .ce-f-row {
  margin-top: 2rem;
}
.sr-only {
  border: 0;
  clip-path: circle(0);
  height: 0px;
  line-height: normal;
  overflow: hidden;
  position: absolute !important;
  width: 0px;
}
.ce-d-flex {
  display: flex;
}
.ce-d-flex * {
  flex-grow: 1;
}
.ce-linear-loader {
  width: 100%;
}
.ce-ll__track,
.ce-ll__fill {
  border-radius: 5rem;
  height: 0.6rem;
  margin: 0;
}
.ce-ll__track {
  background: #d2d3d4;
  display: flex;
  width: 100%;
}
.ce-ll-percentage span {
  transition: width 1s ease-in-out;
}
.ce-ll-indeterminate .ce-ll__track span {
  animation: running-progress 2s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}
.ce-ll__fill-cool {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
}
.ce-ll__fill-warm {
  background: linear-gradient(317.58deg, #fba635 0%, #acd74e 100%);
}
.ce-ll__fill-hot {
  background: linear-gradient(317.58deg, #62248d 0%, #c4356e 100%);
}
@keyframes running-progress {
  0% {
    margin-left: 0px;
    margin-right: 100%;
  }
  50% {
    margin-left: 25%;
    margin-right: 0%;
  }
  100% {
    margin-left: 100%;
    margin-right: 0;
  }
}
.ce-ll__legend {
  color: rgba(0, 0, 0, 0.87);
  display: inline-block;
  margin-bottom: 0.6rem;
}
